/*====================================
    DESKTOP
====================================*/

#nav #desktop {
    z-index: 100;
    position: absolute;
    top: 0;
    left: 0;
    height: 0px;
    overflow: visible;
    width: 100%;
    display: none;
}

#nav #desktop li a {
    font-weight: 300;
    font-size: 18pt;
    text-decoration: none;
    transition: .5s;
    opacity: .9;
}

#nav #desktop .home, #nav #desktop .work, #nav #desktop .contact, #nav #desktop .about {
    position: fixed;
    padding: 40px 4%;
}

#nav #desktop .home {
    top: 0;
    left: 0;
}

#nav #desktop .work {
    top: 0;
    right: 0;
}

#nav #desktop .contact {
    bottom: 0;
    left: 0;
}

#nav #desktop .about {
    bottom: 0;
    right: 0;
}

/*====================================
    DESKTOP EFFECT
====================================*/

#nav nav a {
    position: relative;
    display: inline-block;
    outline: none;
    letter-spacing: 1px;
    font-weight: 400;
    font-size: 1.35em;
}

#nav nav a:hover, #nav nav a:focus {
    outline: none;
}

#nav .cl-effect a {
    overflow: hidden;
    height: 1.3em;
}

#nav .cl-effect a span {
    position: relative;
    display: inline-block;
    transition: transform 0.3s;
}

#nav .cl-effect a span::before {
    position: absolute;
    top: 100%;
    content: attr(data-hover);
    transform: translate3d(0, 0, 0);
}

#nav .dark .cl-effect a span::before {
    color: var(--white);
}

#nav .light .cl-effect a span::before {
    color: var(--black);
}

#nav .cl-effect a:hover span {
    transform: translateY(-100%);
}

/*====================================
    DESKTOP LIGHT / DARK
====================================*/

#nav .dark #desktop .active {
    -webkit-text-decoration-color: var(--white);
            text-decoration-color: var(--white);
}

#nav .light #desktop .active {
    -webkit-text-decoration-color: var(--black);
            text-decoration-color: var(--black);
}

#nav #desktop .active span {
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    text-decoration-thickness: 2px;
}

/*====================================
    MOBILE
====================================*/

