    margin-bottom: 30px;
    width: 70%;
    max-width: 1000px;
}

#project .content .description {
    padding: 0 30px 50px;
}

#project .content .description p {
    font-size: 1.4em;
    font-weight: 300;
    line-height: 1.8em;
    margin: 0;
    padding: 0;
    padding: 0 10px 0;
}

