================================*/

#project .images-container {
    padding: 0 30px 20px;
}

#project .images-container ul.images {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(1fr));
    grid-gap: 15px;
    list-style: none;
    margin: 0;
